import React from 'react';
import { Switch } from 'react-router-dom';
import { Route } from 'react-router-dom/cjs/react-router-dom.min';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import { lazy, Suspense } from 'react';

const CreateWealth = lazy(()=>import('views/marketing/CreateWealth'));
const DesignMintbox = lazy(()=>import('views/marketing/DesignMintbox'));
const FinancialGoals = lazy(()=>import('views/marketing/FinancialGoals'));
const Home = lazy(()=>import('views/marketing/Home'));
const InvestingGenius = lazy(()=>import('views/marketing/InvestingGenius'));
const LongWealthCreator = lazy(()=>import('views/marketing/LongWealthCreator'));
const MintboxBlack = lazy(()=>import('views/marketing/MintboxBlack'));
const Privacy = lazy(()=>import('views/marketing/Privacy'));
const QRCode = lazy(()=>import('views/marketing/QRCode'));
const ShortWealthCreator = lazy(()=>import('views/marketing/ShortWealthCreator'));
const TermConditions = lazy(()=>import('views/marketing/TermConditions'));
const UnderstandLesson = lazy(()=>import('views/marketing/UnderstandLessons'));
const AllFunds = lazy(()=>import('../../marketing/AllFundsDesktop'));
const MarketingRoutes = lazy(()=>import('../components/molecules/Routers/MarketingRoutes'));




const WebHomeLayout = () => {
  return (
    <Switch>

      {/* -------- Route is for MarketingScreens -------- */}
      {/* <Route exact path={`/`} component={HomeSample} /> */}
      <Suspense fallback={<ActivityLoaderMintbox/>}>
      <MarketingRoutes exact path={`/`} component={Home} />
      <MarketingRoutes exact path={`/mintbox-black`} component={MintboxBlack} />
      <MarketingRoutes exact path={`/design-mintbox`} component={DesignMintbox} />
      <MarketingRoutes exact path={`/financial-goal`} component={FinancialGoals} />
      <MarketingRoutes exact path={`/build-mintbox`} component={CreateWealth} />
      <MarketingRoutes exact path={`/long-wealth-creator`} component={LongWealthCreator} />
      <MarketingRoutes exact path={`/short-wealth-creator`} component={ShortWealthCreator} />
      <MarketingRoutes exact path={`/privacy-policy`} component={Privacy} />
      <MarketingRoutes exact path={`/terms-and-conditions`} component={TermConditions} />

      <MarketingRoutes faq={false} exact path={`/investing-masterclass`} component={InvestingGenius} />
      <MarketingRoutes faq={false} exact path={`/understand-lesson`} component={UnderstandLesson} />
      <MarketingRoutes faq={false} headerMode={0} exact path={`/all-funds`} component={AllFunds} />

      <Route path={`/qr-custom/app-link`} component={QRCode} />

      </Suspense>

    </Switch>
  );
};

export default WebHomeLayout;
